___CSS_LOADER_EXPORT___.push([module.id, `.search-product {
  width: fit-content;
  height: auto;
  background-color: rgb(156, 105, 105);
  box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
}
.search {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  width: fit-content;
  height: auto;
  padding: 14px 24px;
}

.search input {
  border-radius: 5px;
  padding: 10px;
}

.search i {
  color: #fff;
  border-radius: 5px;
  padding: 10px;
  position: relative;
}

.filter-popup {
  width: 100%;
  height: 20vh;
  background-color: aquamarine;
  padding: 19px;
  position: absolute;
  top: 0;
  left: 0;
}

.fa-filter {
  font-size: 22px;
  cursor: pointer;
}

.list {
  width: fit-content;
  height: auto;
}

.product-list {
  list-style: none;
  padding: 0;
  margin: 0;
  border-radius: 5px;
}

.product-list li {
  padding: 10px;
}

.product-list li:last-child {
  border-bottom: none;
}
